nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 95px;
    height: 80px;
    background-color: transparent;
    z-index: 9999;
    position: relative;
}

nav img{
    background-color: transparent;
}

#search-bar{
    height: 40px;
    border-radius: 6px;
    border: 1px solid #D1D5DB;
    /* border: none; */
    /* outline: none; */
    width: 540px;
    text-indent: 20px;
    color: white;
    background: transparent;
}

a{
    background: transparent;
}

#search-div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 540px;
    position: relative;
    background: transparent;
    
}

#search-logo{
    position: absolute;
    right: 13px;
    cursor: pointer;
    background-color: transparent;
}


